.m-login {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: initial;
  background: rgba(0, 0, 0, 0.6);
}
.m-login .content {
  overflow: hidden;
  width: 50rem;
  height: 35rem;
  line-height: initial;
  background: #fff;
  border-radius: 0.8rem 0.8rem 0.8rem 0.8rem;
  position: absolute;
  top: 50%;
  right: auto;
  bottom: auto;
  left: 50%;
  z-index: initial;
  margin: -17.5rem 0 0 -25rem;
}
.m-login .content .slide {
  width: 100rem;
  position: relative;
  top: auto;
  right: auto;
  bottom: auto;
  left: 0;
  z-index: initial;
  transition: left 0.2s ease-in;
}
.m-login .content .slide .login-content,
.m-login .content .slide .regist-content {
  float: left;
  width: 50rem;
  height: 35rem;
  line-height: initial;
}
.m-login .content .regist-content .left-content {
  padding: 8rem 4rem 2rem 4rem;
}
.m-login .content .left-content,
.m-login .content .right-content {
  width: 60%;
  float: left;
  height: 35rem;
  line-height: initial;
  padding: 12rem 4rem 2rem 4rem;
}
.m-login .content .right-content {
  width: 40%;
  border-left: 0.1rem solid #e5e5e5;
}
.m-login .content .input-item {
  border: 0.1rem solid #e5e5e5;
  border-radius: 0.4rem 0.4rem 0.4rem 0.4rem;
  height: 3.6rem;
  line-height: initial;
  margin: 1.6rem 0 1.6rem 0;
  position: relative;
  top: auto;
  right: auto;
  bottom: auto;
  left: auto;
  z-index: initial;
  padding: 0.8rem 0.8rem 0.8rem 4rem;
  overflow: hidden;
}
.m-login .content .input-item .icon {
  text-align: center;
  width: 3.6rem;
  height: 3.4rem;
  line-height: 3.4rem;
  position: absolute;
  top: 0;
  right: auto;
  bottom: 0;
  left: 0;
  z-index: initial;
  background: #e5e5e5;
}
.m-login .content .input-item input {
  outline: none;
  cursor: pointer;
  width: 100%;
  border: none;
  height: 2rem;
  line-height: 2rem;
}
.m-login .content .login-button,
.m-login .content .regist-button {
  cursor: pointer;
  color: #fff;
  text-align: center;
  font-size: 1.6rem;
  background: #00A0E8;
  border-radius: 0.4rem 0.4rem 0.4rem 0.4rem;
  height: 3.6rem;
  line-height: 3.6rem;
}



