.m-dropdown {
  display: inline-block;
  height: 6rem;
  min-width: 6rem;
  text-align: center;
  position: relative;
}
.m-dropdown .login {
  color: #fff;
  cursor: pointer;
  display: inline-block;
  background: green;
  height: 3rem;
  line-height: 3rem;
  padding: 0 1.6rem 0 1.6rem;
  margin: 1.5rem 0 0 2rem;
  border-radius: 0.4rem 0.4rem 0.4rem 0.4rem;
}
.m-dropdown .user {
  display: inline-block;
  width: 4rem;
  height: 4rem;
  border-radius: 4rem;
  margin-top: 1rem;
  background: url(user-logo.jpeg?exports) 0 0 / cover no-repeat;
}
.m-dropdown .user:hover + .menu-list,
.m-dropdown .user + .menu-list:hover {
  display: block;
}
.m-dropdown .menu-list {
  display: none;
  position: fixed;
  top: 5rem;
  right: auto;
  bottom: auto;
  left: auto;
  z-index: initial;
  padding: 2rem 0 0 0;
}
.m-dropdown .menu-list ul {
  border: 0.1rem solid #e5e5e5;
  background: #fff;
  width: 12rem;
  border-radius: 0.4rem 0.4rem 0.4rem 0.4rem;
}
.m-dropdown .menu-list ul li {
  cursor: pointer;
  text-align: left;
  padding: 0 0 0 1.5rem;
  height: 3rem;
  line-height: 3rem;
}
.m-dropdown .menu-list ul li:hover {
  background: #f3f7f6;
}
.m-dropdown .menu-list ul li:first-child {
  border-radius: 0.4rem 0.4rem 0 0;
}
.m-dropdown .menu-list ul li:last-child {
  border-radius: 0 0 0.4rem 0.4rem;
}
.m-dropdown .menu-list ul li.sep {
  height: 0.1rem;
  line-height: initial;
  background: #e5e5e5;
}



